@import "../../variables.scss";

.benefits {
    position: relative;

    &__i {
        position: absolute;
        // width: 950px;
        // overflow: hidden;
        left: calc(50% - 475px); //950px / 2
        font-family: 'Poppins', sans-serif;
        -webkit-text-stroke: 1px rgba(214, 214, 214, 0.5);
        letter-spacing: 1px;
        line-height: 144px;
        font-weight: 900;
        font-size: 144px;
        text-transform: uppercase;
        color: transparent;
        user-select: none;
    }

    & .logo {
        width: fit-content;
        margin-left: auto;
        margin-right: auto;
        top: 60px;
    }

    &__title {
        position: relative;
        top: 32px;
        padding: 20px 0;
        font-size: 36px;
        font-weight: 400;
        text-align: center;
    }

    &__grid {
        margin-top: 0px;
        padding: 70px 0;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }

    &__grid-item {
        position: relative;
        width: 370px;
        height: 262px;
        padding: 20px;
        margin-bottom: 70px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;
        background-color: #fff;
        transition: transform 0.3s ease-in;
        -webkit-transition: transform 0.3s ease-in, perspective 0.3s ease-in;
        box-shadow: -45px 12px 38px 0px rgba(0, 0, 0, 0.4);
        transform: skew(3deg, 3deg) rotate3d(1, 1, 1, -5deg);
        -webkit-transform: skew(3deg, 3deg) rotate3d(1, 1, 1, -5deg);
        font-style: italic;


        &:hover {
            transform: perspective(600) skew(3deg, 3deg) rotate3d(1, 1, 1, -5deg);
            -webkit-transform: perspective(600) skew(3deg, 3deg) rotate3d(1, 1, 1, -5deg);
        }

        &:before {
            content: "";
            position: absolute;
            left: -15px;
            top: -1px;
            width: 15px;
            height: 262px;
            background-color: $juicy-green;
            clip-path: polygon(0 0, 100% 100%, 100% 0);
        }

        &:after {
            content: "";
            position: absolute;
            top: -15px;
            left: -15px;
            width: 370px;
            height: 15px;
            background-color: $juicy-green;
            clip-path: polygon(0 0, 0% 100%, 100% 100%);
        }
    }

    &__grid-icon {
        width: 70px;
        height: 70px;
    }

    &__grid-text {
        text-align: center;
    }
}

.benefits__grid-item:hover .benefits__grid-item::before{
    transform: rotate(20deg);
}

@media only screen and (max-width: 600px){
    .benefits {
        position: relative;
    
        &__i {
            // position: absolute;
            // width: 950px;
            // overflow: hidden;
            left: calc(50% - 233px); //466px / 2
            // font-family: 'Poppins', sans-serif;
            // -webkit-text-stroke: 1px rgba(214, 214, 214, 0.5);
            letter-spacing: 1px;
            line-height: 110px;
            // font-weight: 900;
            font-size: 70px;
            // text-transform: uppercase;
            // color: transparent;
            // user-select: none;
        }
    
        & .logo {
            top: 53px;
        }
    
        &__title {
            top: 32px;
            padding: 20px 0 0 0;
            font-size: 24px;
            font-weight: 500;
        }
    
        &__grid {
            margin-top: 0px;
            padding: 70px 0;
            justify-content: center;
        }
    
        &__grid-item {
            width: 300px;
            height: 190px;
            padding: 3px;
            margin-bottom: 30px;
            box-shadow: -15px -15px 0px -5px #ff7029;
            transform: skew(0deg, 0deg) rotate3d(0, 0, 0, 0deg);
            -webkit-transform: skew(0deg, 0deg) rotate3d(0, 0, 0, 0deg);
            font-style: normal;
            filter: drop-shadow(-6px 0px 8px #252525);
    
    
            &:hover {
                transform: skew(0deg, 0deg) rotate3d(0, 0, 0, 0deg);
                -webkit-transform: skew(0deg, 0deg) rotate3d(0, 0, 0, 0deg);
            }
    
            &:before {
                content: "";
                position: absolute;
                left: -5px;
                top: -5px;
                width: 5px;
                height: 185px;
                background-color: $juicy-orange;
                clip-path: unset;
                filter: grayscale(1);
            }
    
            &:after {
                content: "";
                position: absolute;
                top: -5px;
                left: -5px;
                width: 295px;
                height: 5px;
                background-color: $juicy-orange;
                clip-path: unset;
                filter: grayscale(1);
            }
        }
    
        &__grid-icon {
            width: 60px;
            height: 60px;
        }
    }
    
    .benefits__grid-item:hover .benefits__grid-item::before{
        transform: rotate(20deg);
    }
}