@import "../../variables.scss";

    .flip-card {
        background-color: transparent;
        perspective: 1000px;
        position: relative;
        width: 350px;
        height: 300px;
        margin-left: 10px;
        margin-right: 10px;

        &:nth-of-type(2n+1) {
            margin-top: 30px;
        }

        &__inner {
            position: relative;
            width: 100%;
            height: 100%;
            text-align: center;
            transition: transform 1s;
            transform-style: preserve-3d;

            &:hover .icon, &:hover .flip-card__front::before {
                opacity: 0;
            }
        }

        &:hover &__inner {
            transform: rotateY(180deg);
          }

        &__front, &__back {
            position: absolute;
            width: 100%;
            height: 100%;
            -webkit-backface-visibility: hidden; /* Safari */
            backface-visibility: hidden;
        }
           
        &__front {
            width: 100%;
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: space-evenly;
            align-items: flex-start;
            padding: 50px 20px 20px;
            background-color: $dark-bg;
            background-size: 30%;
            background-repeat: no-repeat;
            background-position: 100% 0;
            box-shadow: 9px 12px 38px 0px rgba(0, 0, 0, 0.4);
            color: #fff;
            font-size: 20px;
            line-height: 1.5;
            overflow: hidden;

            &:before {
                content: '';
                position: absolute;
                display: inline-block;
                top: 30px;
                width: 36px;
                height: 2px;
                background-color: #D4D4D4;
                transition: opacity 1s;
            }

            & .icon {
                position: absolute;
                fill: rgba(211, 211, 211, 0.2);
                z-index: 0;
                transition: opacity 1s;
            }
        }

        &__info {
            text-shadow: 1px 1px 2px rgb(0, 0, 0);
            text-align: left;
        }

        &__back {
            background-color: $light-dark-bg;
            color: #000;
            transform: rotateY(180deg);
            text-align: left;
            padding: 15px;
            font-size: 17px;
            font-weight: 300;
            overflow-y: auto;
            box-shadow: inset 1px 1px 5px 0px rgba(0, 0, 0, 1);
        }

        &__i {
            width: 45px;
            height: 45px;
            margin-top: 20px;
            text-align: center;
            line-height: 45px;
            border-radius: 50% 50% 50% 50%;
            background-color: $juicy-green;
            color: #FFFFFF;
            font-weight: 900;
            box-shadow:  3px 3px 16px 0px rgba(0, 0, 0, 0.2);
            opacity: 1;
            transition: transform 0.5s ease-out, background-color 1s, border-color 1s, color 1s ;
            cursor: pointer;
        }
    }

    @media only screen and (max-width: 600px){
        .flip-card {
            width: 170px;
            height: 200px;
            margin: 10px 0;
            
            &:nth-of-type(2n+1) {
                margin-top: 10px;
            }
               
            &__front {
                padding: 20px 20px 20px;
                background-size: 30%;
                background-position: 100% 0;
                font-size: 16px;
                line-height: 25px;
    
                &:before {
                    top: 15px;
                    width: 20px;
                    height: 1px;
                }
    
                & .icon {
                    width: 20px;
                    position: absolute;
                    fill: rgba(211, 211, 211, 0.2);
                    z-index: 0;
                    transition: opacity 1s;
                }
            }
    
            &__info {
                text-align: left;
            }
    
            &__back {
                background-color: $light-dark-bg;
                color: #000;
                transform: rotateY(180deg);
                text-align: justify;
                padding: 15px;
                font-size: 12px;
                font-weight: 300;
                overflow-y: auto;
                box-shadow: inset 1px 1px 5px 0px rgba(0, 0, 0, 1);
            }
    
            &__i {
                width: 35px;
                height: 35px;
                margin-top: 0px;
                line-height: 35px;
            }
        }
    }