@import "../../variables.scss";

.banner {
    height: 100vh;
    position: relative;
    overflow: hidden;

    &__dark-layer {
        width: 53%;
        height: 100%;
        position: relative;
        top: 0;
        left: 47%;
        background-color: $dark-bg;
        background-repeat: no-repeat;
        background-position: center right;
        background-size: auto 80%;
        clip-path: polygon(30% 0%, 100% 0%, 100% 100%, 0% 100%);
    }

    &__info {
        position: absolute;
        color: #fff;
        width: fit-content;
        text-align: end;
        background-color: $dark-bg;
        z-index: 10;
    }

    &__info--main {
        top: 40vh;
        right: 8vw;
        font-size: 34px;
        text-shadow: 5px 5px 3px #000;
    }

    &__info--details {
        bottom: 2vh;
        right: 50px;
        font-size: 21px;
    }

    &__action {
        position: relative;
        width: 250px;
        height: 50px;
        color: #fff;
        font-size: 15px;
        border: 2px solid #fff;
        transform: skewX(-15deg);
        // border: none;
        // box-shadow:  3px 3px 16px 0px rgba(0, 0, 0, 1);
        outline-style: none;
        background: transparent;
        cursor: pointer;

        &::before {
            content: "";
            position: absolute;
            top: -2px;
            left: -2px;
            width: 0px;
            height: 50px;
            background-color: transparent;
            border: 2px solid $juicy-orange;
            -webkit-transition: width 0.3s ease-out 0.3s;
            -moz-transition: width .3s ease-out 0.3s;
            -o-transition: width .3s ease-out 0.3s;
            transition: width .3s ease-out 0.3s;
        }

        &::after {
            content: "";
            position: absolute;
            top: -2px;
            left: -15px;
            border: 2px solid $juicy-orange;
            width: 0px;
            height: 50px;
        }

    }

    &__action:hover::before {
        width: 250px;
    }
}

@media only screen and (max-width: 600px) {
    .banner {
        height: 500px;
        background-size: auto 150%;
        background-repeat: no-repeat;
        background-position: 0%;
        -webkit-animation: banner_animate 25s infinite ease-in-out alternate;
        animation: banner_animate 25s infinite ease-in-out alternate;

    
        &__dark-layer {
            display: none;
            width: 53%;
            height: 100%;
            position: relative;
            top: 0;
            right: 0;
            // background-color: $dark-bg;
            // background-repeat: no-repeat;
            // background-position: center right;
            background-size: auto 80%;
            // clip-path: polygon(0 0, 100% 76%, 100% 100%, 0 100%);
        }
    
        &__info {
            // position: absolute;
            // color: #fff;
            // width: fit-content;
            text-align: center;
            background-color: #20242ccf;
            // z-index: 10;
        }
    
        &__info--main {
            width: 100%;
            height: 40%;
            top: 145px;
            right: 0;
            border: 2px solid #fff;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            font-size: 26px;
            text-shadow: 5px 5px 3px #000;
            line-height: 24px;
        }
    
        &__info--details {
            width: 100%;
            bottom: 0;
            right: 0;
            font-size: 22px;
            // text-align: center;
        }
    
        &__action {
            position: relative;
            width: 250px;
            height: 50px;
            color: #fff;
            font-size: 15px;
            border: 2px solid #fff;
            transform: skewX(-15deg);
            // border: none;
            // box-shadow:  3px 3px 16px 0px rgba(0, 0, 0, 1);
            outline-style: none;
            background: transparent;
            cursor: pointer;
    
            &::before {
                content: "";
                position: absolute;
                top: -2px;
                left: -2px;
                width: 0px;
                height: 50px;
                background-color: transparent;
                border: 2px solid $juicy-orange;
                -webkit-transition: width 0.3s ease-out 0.3s;
                -moz-transition: width .3s ease-out 0.3s;
                -o-transition: width .3s ease-out 0.3s;
                transition: width .3s ease-out 0.3s;
            }
    
            &::after {
                content: "";
                position: absolute;
                top: -2px;
                left: -15px;
                border: 2px solid $juicy-orange;
                width: 0px;
                height: 50px;
            }
    
        }
    
        &__action:hover::before {
            width: 250px;
        }
    }
}

@keyframes banner_animate {
    0% {
        background-position: 40%;
    }

    30% {
        background-position: 50%;
    }

    70% {
        background-position: 50%;
    }

    100% {
        background-position: 60%;
    }
}